import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppComponent } from './app.component';
import { LoginPageComponent } from './login-page/login-page.component';
import { LandingPageComponent } from './landing-page/landing-page.component';
import { LicAuthenticationComponent } from './lic-authentication/lic-authentication.component';

import { Routes, RouterModule } from '@angular/router';
import { CustomerInfoComponent } from './customer-info/customer-info.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

import { MatSelectModule } from '@angular/material/select';
import { DragDropModule } from '@angular/cdk/drag-drop';
import { BasicInfoComponent } from './basic-info/basic-info.component';
import { AdvanceInfoComponent } from './advance-info/advance-info.component';
import { DeviceInfoComponent } from './device-info/device-info.component';
import { FormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';

const appRouter: Routes = [
  { path: 'info', component: CustomerInfoComponent },    // http://localhost:4200/info
  { path: 'login', component: LoginPageComponent },      // http://localhost:4200/login
  { path: 'landing', component: LandingPageComponent },  // http://localhost:4200/landing
  { path: '', component: LicAuthenticationComponent }    // http://localhost:4200/lic
];

@NgModule({
  declarations: [
    AppComponent,
    LoginPageComponent,
    LandingPageComponent,
    LicAuthenticationComponent,
    CustomerInfoComponent,
    BasicInfoComponent,
    AdvanceInfoComponent,
    DeviceInfoComponent
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(appRouter),
    BrowserAnimationsModule,
    DragDropModule,
    MatSelectModule,
    FormsModule,
    HttpClientModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
