import {Component, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {HttpClient} from '@angular/common/http';

@Component({
  selector: 'app-lic-authentication',
  templateUrl: './lic-authentication.component.html',
  styleUrls: ['./lic-authentication.component.css']
})
export class LicAuthenticationComponent implements OnInit {

  constructor(private route: ActivatedRoute, private router: Router, private http: HttpClient) {
  }

  ngOnInit(): void {
    console.log('LicAuthenticationComponent Initiated');
    this.validateToken();
  }

  validateToken() {

    const userName = this.route.snapshot.queryParams.username;
    const token = this.route.snapshot.queryParams.token;

    this.http.post(
      'http://localhost:8080/validatetoken',
      {
        userName,
        token
      }
    ).subscribe(response => {
        console.log(response);
        // if (response.isValid) {
        //   this.router.navigate(['/landing']);
        // } else {
        //   this.router.navigate(['/login']);
        // }
      },
      error => {
        console.log(error);
        this.router.navigate(['/login']);
      }
    );


  }

}
