import {Component, OnInit} from '@angular/core';
import {Router} from '@angular/router';
import {CdkDragDrop, moveItemInArray} from '@angular/cdk/drag-drop';
import {FormControl} from '@angular/forms';

@Component({
  selector: 'app-customer-info',
  templateUrl: './customer-info.component.html',
  styleUrls: ['./customer-info.component.css']
})
export class CustomerInfoComponent implements OnInit {

  selectedComponents: any;
  componentNameList: string[] = ['basic-info', 'advance-info', 'device-info'];

  allComponents = [
    {id: 1, name: 'basic-info', displayName: 'Basic Info'},
    {id: 2, name: 'advance-info', displayName: 'Advance Info'},
    {id: 3, name: 'device-info', displayName: 'Device Info'}
  ];

  showComponent = [];

  // showComponent = [
  //   {id: 1, name: 'basic-info', displayName: 'Basic Info'},
  //   {id: 2, name: 'advance-info', displayName: 'Advance Info'},
  //   {id: 3, name: 'device-info', displayName: 'Device Info'}
  // ];

  drop(event: CdkDragDrop<string[]>) {
    moveItemInArray(this.showComponent, event.previousIndex, event.currentIndex);
  }

  constructor(private router: Router) {
  }

  ngOnInit(): void {
  }

  logoutHandler() {
    this.router.navigate(['/login']);
  }

  crmHandler() {
    this.router.navigate(['/info']);
  }

}
